import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'decimales'
})
export class DecimalesPipe implements PipeTransform {

  transform(value: number, locale: string): any {
    if (value === 0) {
      return 0
    } else {
      return new Intl.NumberFormat(locale, {
        minimumFractionDigits: 1
      }).format(value);
    }
  }

}
