import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ModalController, AlertController } from '@ionic/angular';
import { InventariosService } from 'src/app/providers/Api/Inventarios.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { IvtModalPage } from '../ivt-modal/ivt-modal.page';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ivtcategorias',
  templateUrl: './ivtcategorias.page.html',
  styleUrls: ['./ivtcategorias.page.scss'],
})
export class IVTcategoriasPage implements OnInit {

  categorias = [];
  load = false;

  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toast: ToastServices,
    private alertCtrl: AlertController,
    private activeRoute: ActivatedRoute
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.cargarCategorias();
  }

  refrescando( event ) {
    this.cargarCategorias().then(() => {
      event.target.complete()
    }).catch(() => event.target.complete())
  }

  async cargarCategorias() {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.ivtApi.leerCategoria(info.token, this.activeRoute.snapshot.paramMap.get('id')).subscribe((data: any) => {
          if (data.exe) {
              this.categorias = data.response;
          } else {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
          }
        }, (err) => {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
        })
      }
  }

  async modal() {
    const modal = await this.modalCtrl.create({
      component: IvtModalPage,
      componentProps: {
        tipo: 'categorias',
        inventario: this.activeRoute.snapshot.paramMap.get('id')
      },
    })

    await modal.present();

    const cerrado = await modal.onWillDismiss();

    if (cerrado.data) {
      if (cerrado.data.exe) {
        this.cargarCategorias();
      }
    }

  }

  async editar ( categoria: any, i ) {
      const alert = await this.alertCtrl.create({
        header: 'Editar Categoría',
        inputs: [
          {
            type: 'text',
            name: 'nombre',
            placeholder: 'Escriba el nombre de la categoría',
            value: categoria.nombre
          }
        ],
        buttons: [
          {
            text: 'Cancelar',
            role: 'cancel'
          },
          {
            text: 'Aceptar',
            handler: async (data) => {
                const info = await this.almacenamiento.obtenerToken();

                if ( info ) {
                  // tslint:disable-next-line:max-line-length
                  this.ivtApi.editarCategoria(info.token, {nombre: data.nombre, _id: categoria._id}).subscribe((res: any) => {
                      
                      if ( res.exe ) {
                        this.toast.toastExitoso('Categoría editado!');
                        this.categorias[i].nombre = res.response.nombre;
                      } else {
                        this.toast.toastError('No se pudo editar la categoría, intentelo más tarde');
                      }
                  }, (err) => {
                    this.toast.toastError('No se pudo editar la categoría, intentelo más tarde');
                  })
                }
            }
          }
        ]
      })

      await alert.present();

     
  }

  async eliminar ( categoria: any, i ) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      // tslint:disable-next-line:max-line-length
      this.ivtApi.eliminarCategoria(info.token, categoria._id).subscribe((res: any) => {
          
          if ( res.exe ) {
            this.toast.toastExitoso('Categoría eliminada!');
            this.categorias.splice(i, 1);
          } else {
            this.toast.toastError('No se pudo eliminar la categoría, intentelo más tarde');
          }
      }, (err) => {
        this.toast.toastError('No se pudo eliminar la categoría, intentelo más tarde');
      })
    }
  }

}
